@font-face {
    font-family: "Montserrat";
    src: local("Montserrat"),
      url("../fonts/Montserrat-Regular.ttf") format("truetype");
  }
  
  @font-face {
    font-family: "Montserrat-Black";
    src: local("Montserrat-Black"),
      url("../fonts/Montserrat-Black.ttf") format("truetype");
  }

  @font-face {
    font-family: "Montserrat-Bold";
    src: local("Montserrat-Bold"),
      url("../fonts/Montserrat-Bold.ttf") format("truetype");
  }
  
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  
  input[type="number"] {
    -moz-appearance: textfield;
  }
  
  * {
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-decoration: none;
  }
  
  html {
    min-width: 360px;
    margin-left: auto;
    margin-right: auto;

  }
  
  .wrapper {
    width: 69%;
    margin-left: auto;
    margin-right: auto;
  }
  
  /*header*/
  
  .header_swap {
    position: relative;
  }
  
  .header-nav-bar_swap {
    width: 70.417vw;
    background-color: rgba(132, 132, 132, 0.1);
    display: flex;
    margin-top: 1vw;
    border-radius: 0.518vw;
    margin-left: auto;
    margin-right: auto;
  }
  
  .orange-circle {
    position: absolute;
    right: 0vw;
    top: -8vw;
    z-index: -1;
    width: 47.76vw;
    height: 47.76vw;
  }
  
  .header-nav_swap {
    width: 100%;
    padding: 1vw 1.5vw;
    display: flex;
  }
  
  .axiomlogo_swap {
    width: 12.031vw;
  }
  
  .button_trade {
    width: 2.383vw;
    height: 2.383vw;
  }
  
  .header-nav_swap nav {
    padding-left: 1.5vw;
    width: 53.4vw;
    justify-content: space-between;
    display: flex;
  }
  
  .header-nav_swap nav a {
    font-family: "Montserrat";
    font-weight: 300;
    color: #fff;
    font-size: 0.829vw;
    line-height: 2.4vw;
  }
  
  .button_swap {
    width: 11.35vw;
    font-size: 0.833vw;
    border-radius: 8px;
    background: rgb(93, 187, 100);
    background: linear-gradient(
      90deg,
      rgba(93, 187, 100, 1) 0%,
      rgba(38, 144, 68, 1) 100%
    );
    color: #fff;
    border: none;
  }
  
  .left_orange_circle {
    position: absolute;
    left: 0;
    top: 0vw;
    width: 32vw;
    z-index: -1;
  }
  
  .board {
    width: 9.111vw;
    display: flex;
    justify-content: space-between;
  }
  
  .board p {
    font-size: 0.982vw;
    color: #fff;
    font-weight: bold;
    filter: drop-shadow(0 0 0.75vw #fff);
    margin: 0;
    padding: 0;
    letter-spacing: 0.12vw;
    padding-top: 0.5vw;
  }
  
  .board img {
    width: 0.848vw;
    height: 1.1vw;
    filter: drop-shadow(0 0 0.75vw #1ed226);
    padding-top: 0.6vw;
  }
  
  /*dashboard*/
  
  .dashboard {
    width: 98%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 5.23vw;
  }
  
  .dashboard-conteiner {
  }
  
  .dashboard-conteiner-title {
    padding-bottom: 2vw;
  }
  
  .dashboard-conteiner-title h1 {
    font-family: 'Montserrat-Bold';
    color: #fff;
    font-size: 2.19vw;
    line-height: 2.5vw;
    font-weight: bold;
  
  }
  
  .dashboard-conteiner-title-line {
    width: 9vw;
    height: 1.5vw;
    border-bottom: 0.1vw solid #31b536;
  }
  
  .dashboard-conteiner-content {
    display: flex;
    justify-content: space-between;
  }
  
  .conteiner-content-briefcase {
    width: 40vw;
    height: 24.427vw;
    background-color: rgba(112, 112, 112, 0.1);
    border-radius: 0.518vw;
    position: relative;
    border: 0.15vw solid rgba(255, 255, 255, 0.1);
  }
  
  .conteiner-content-briefcase h2,
  .conteiner-content-deal h2 {
    font-family: "Montserrat-bold";
    color: #fff;
    font-size: 1.04vw;
    line-height: 2vw;
    font-weight: 700;
    text-align: center;
    padding-top: 1vw;
  }
  
  .conteiner-content-briefcase img {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1.554vw;
    width: 16.632vw;
  }
  
  img.ellipse_one,
  img.ellipse_two,
  img.ellipse_three,
  img.ellipse_fore {
    position: absolute;
  }
  
  img.ellipse_one {
    top: -1.2vw;
    left: 16.8vw;
    width: 12.24vw;
  }
  
  img.ellipse_two {
    top: 0vw;
    left: 10.9vw;
    width: 5.885vw;
  }
  
  img.ellipse_three {
    top: 8.8vw;
    left: 11.2vw;
    width: 13.698vw;
  }
  
  img.ellipse_fore {
    top: 9vw;
    left: 24.7vw;
    width: 4.167vw;
  }
  
  .conteiner-content-deal {
    width: 24.8vw;
    height: 24.427vw;
    background-color: rgba(132, 132, 132, 0.1);
    border-radius: 1.354vw;
    border: 0.15vw solid rgba(112, 112, 112, 0.1);
  }
  
  .deal-input-content {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .content-deal-input {
    width: 81%;
    margin-left: auto;
    margin-right: auto;
    padding: 1vw 0;
    margin-top: 1vw;
    border-top: 0.1vw solid rgba(222, 231, 237, 0.3);
    border-bottom: 0.1vw solid rgba(222, 231, 237, 0.3);
  }
  
  .deal-input {
    width: 100%;
    height: 3.125vw;
    border-radius: 0.259vw;
    border: 0.1vw solid rgba(255, 255, 255, 0.3);
    display: flex;
  }
  
  .deal-input input {
    /* margin-top: 0.5vw; */
    margin-left: 2vw;
    width: 100%;
    height: 1.2vw;
    background-color: transparent;
    border: none;
    outline: none;
    color: #fff;
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 0.938vw;
  }
  
  ::placeholder {
    color: #fff;
  }
  
  .deal-input p {
    font-family: "Montserrat";
    font-size: 0.573vw;
    margin-left: 2vw;
    margin-top: 0.2vw;
    color: #fff;
  }
  
  .deal-select {
    width: 7.396vw;
    height: 100%;

    background-color: rgba(255, 255, 255, 0.05);
    margin-left: auto;
    display: flex;;
  }
  
  .deal-select-fli {
    width: 9.115vw;
  }
  
  .deal-select-token {
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* width: 7vw;
    margin-left: 0vw; */
  }
  
  .deal-select-token img {
    width: 1.563vw;
    padding-left: 1vw;
  }
  
  .deal-select-token p {
    font-family: "Montserrat";
    font-size: 0.673vw;
    margin-left: 0.5vw;
    /* margin-top: 1.2vw; */
  }
  
  .down-chevron {
    width: 0.573vw;
    margin-left: auto;
    padding-right: 0.8vw;
    padding-top: 0.2vw;
  }
  
  .content-deal-input h3 {
    color: #fff;
    font-family: "Montserrat";
    font-size: 0.573vw;
    font-weight: 400;
    margin-left: .5vw;
    padding-top: 0.5vw;
  }
  
  .content-deal-input span {
    color: #31b536;
  }
  
  .vector_swap {
    display: flex;
    width: 1.414vw;
    margin-left: auto;
    padding-bottom: 1vw;
    margin-right: 3.5vw;
    cursor: pointer;
  }
  
  .conteiner-content-button {
    width: 81%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1vw;
  }
  
  .content-button {
    width: 100%;
    height: 3.125vw;
    font-size: 0.833vw;
    border-radius: 0.259vw;
    background: #01c213;
    color: #fff;
    border: none;
    cursor: pointer;
  
    font-size: 14px;
    font-family: 'Montserrat';
    font-weight: 600;
  }
  
  .content-button.text {
    font-size: 0.833vw;
    font-weight: 300;
  }
  
  .content-button.wrong-network {
    background: #e76d6d;
  }
  
  .content-button.inactive {
    background: #b3b3b3;
    cursor: not-allowed;
  }
  
  .parameters-conteiner,
  .parameters-conteiner-vector {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    flex-direction: column;
  }
  
  .parameters-conteiner-vector img {
    width: 68.448vw;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 3vw;
  }
  
  .parameters-conteiner-content,
  .content_item {
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    justify-content: space-between;
    gap: 1vw;
  }
  
  .parameters-conteiner-content {
    position: relative;
    width: 100%;
  }
  
  .parameters-conteiner-content-mobie {
    display: none;
  }
  
  .content_item {
    padding-top: 2.604vw;
    /* width: 11.799vw; */
  }
  
  .content_item img {
    width: 5.104vw;
  }
  
  .content_item h3 {
    /* padding-top: 0.8vw; */
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 1.042vw;
    color: #fff;
  }
  
  .upward_shift {
    display: none;
    width: 3.021vw;
    height: 3.021vw;
    position: absolute;
    right: -13vw;
    top: 2vw;
  }

  .up{
    position: absolute;
    right: 0;
    width: 3.02vw;
    top: 48.7vw;
    right: 2vw;
    z-index: 1;
  }

  .btc{
    display: flex;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  
  .vector_smart_object {
    position: absolute;
    width: 38.76vw;
    top: 3vw;
    left: 0;
    z-index: -2;
  }
  
  /*transactionHistory*/
  
  .transactionHistory {
    width: 98%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 7.5vw;
  }
  
