    padding-bottom: 1.5vw;
  }
  
  .transactionHistory-conteiner-title h2 {
    font-family: "Montserrat-bold";
    color: #fff;
    font-size: 1.302vw;
    line-height: 2vw;
    font-weight: 700;
  }
  
  .transactionHistory-conteiner-title-line {
    width: 9.063vw;
    height: 1vw;
    border-bottom: 0.1vw solid #31b536;
  }
  
  .transactionHistory-conteiner-content {
    width: 100%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
  }
  
  .transactionHistory-conteiner-content-mobile {
    display: none;
  }
  
  .transactionHistory-conteiner-content table {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
  }
  
  .transactionHistory-conteiner-content table tr td {
    text-align: center;
    padding-top: 1.5vw;
    padding-bottom: 1.5vw;
    font-size: 0.833vw;
    color: #fff;
    font-family: "Montserrat";
    line-height: 1vw;
    font-weight: 400;
  }
  
  .transactionHistory-conteiner-content table tr.content-table-title td {
    color: #31b536;
    padding-top: 2.4vw;
    padding-bottom: 2.4vw;
    font-size: 0.833vw;
    font-family: "Montserrat-bold";
    line-height: 1vw;
    font-weight: 700;
    border-left: none;
    border-right: none;
  }
  
  .content-table-background {
    background-color: rgba(255, 255, 255, 0.1);
  }
  
  .right_green_circle {
    position: absolute;
    width: 36vw;
    right: 0;
    top: 30vw;
    z-index: -1;
  }
  
  .left_green_circle {
    position: absolute;
    left: 0;
    top: 0vw;
    z-index: -1;
