    background-color: rgba(29, 29, 29, 0.9);
    width: 100%;
    z-index: 1;
  }
  
  .strategy {
    display: flex;
    align-items: center;
    padding: 0.521vw 0.26vw;
  }
  
  .strategy:last-child {
