@font-face {
    font-family: 'Montserrat';
    src: local('Montserrat'),
      url('../fonts/Montserrat-Regular.ttf') format('truetype');
  }
  
  @font-face {
    font-family: 'Montserrat-Bold';
    src: local('Montserrat-Bold'),
      url('../fonts/Montserrat-Bold.ttf') format('truetype');
  }
  
  @font-face {
    font-family: 'Montserrat-Light';
    src: local('Montserrat-Light'),
      url('../fonts/Montserrat-Light.ttf') format('truetype');
  }
  
  a{
    color: #fff;
  }
  
  *{
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-decoration: none
  }
  
  html{
    min-width: 360px;
    margin-left: auto;
    margin-right: auto;
    background-color: #000;
  }
  
  .wrapper {
    width: 67.45%;
    margin-left: auto;
    margin-right: auto;
  }
  
  
  
  /*header*/
  
  .burger{
    display: none;
  }
  
  .header{
    position: relative;
  }
  
  .header-nav-bar{
    width: 70.42vw;
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    margin-top: 1vw;
    border-radius: 0.518vw;
    margin-left: auto;
    margin-right: auto;
    position: relative;
  }
  
  
  .header-nav{
    width: 100%;
    padding: 1vw 1.5vw;
    display: flex;
  }
  
  .axiomlogo{
    padding-top: 0.2vw;
    width: 11.82vw;
  }
  
  .header-nav nav{
    padding-left: 3.2vw;
    width: 52.29vw;
    justify-content: space-between;
    display: flex;
  }
  
  .header-nav nav a{
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 0.829vw;
    line-height: 2.4vw;
  }
  
  .drop-down img{
    padding-left: 0.5vw;
    width: 0.52vw;
    padding-bottom: 0.1vw;
  }
  
  .button{
    width: 6.30vw;
    font-size: 0.829vw;
    border-radius: 4px;
    background: linear-gradient(90deg, #EF6B1F 0%, #D64F02 100%);
    color: #fff;
    border: none;
    margin-left: 0.5vw;
    cursor: pointer;
  }
  
  .vector{
    position: absolute;
    z-index: -1;
    padding: 0;
    margin: 0;
    top: -1vw;
    width: 38vw;
  }
  
  .ellipse_header{
    position: absolute;
    z-index: -1;
    padding: 0;
    margin: 0;
    top: -1vw;
    right: 0;
    width: 47.76vw;
  }
  
  /*HowWeWork*/
  
  div.mobile{
    display: none;
  }
  
  .social-conteiner{
    position: absolute;
    z-index: 0;
    left: 2vw;
    top: 21.2vw;
    flex-direction: column;
  }
  
  .social{
    display: flex;
    flex-direction: column;
  }
  
  .social img{
    padding-bottom: 1.82vw;
  }
  
  .facebook{
    width: 0.73vw;
  }
  
  .youtube{
    width: 1.04vw;
  }
  
  .instagram{
    width: 1.04vw;
  }
  
  .x{
    width: 1.15vw;
  }
  
  .telegram{
    width: 1.15vw;
  }
  
  .ellipse_howWeWork{
    position: absolute;
    right: 0;
    width: 30vw;
    top: 53vw;
    z-index: -1;
  }
  
  .ellipse_howWeWorkTwo{
    position: absolute;
    left: 0;
    width: 30vw;
    top: 73vw;
    z-index: -1;
  }

  a{
    text-decoration: none;
  }
  
  .up{
    position: absolute;
    right: 0;
    width: 3.02vw;
    top: 54.7vw;
    right: 2vw;
    z-index: -1;
  }
  
  .howWeWork-conteiner{
    padding-top: 16vw;
  }
  
  .howWeWork-conteiner h2{
    font-family: 'Montserrat-Bold';
    font-weight: 700;
    color: #fff;
    font-size: 2.08vw;
  }
  
  .howWeWork-conteiner-tiltle-mobile{
    display: none;
  }
  
  
  .howWeWork-conteiner p{
    font-family: 'Montserrat-Light';
    font-weight: 300;
    color: #fff;
    font-size: 1.04vw;
    line-height: 2.4vw;
    padding-top: 2.60vw;
    padding-bottom: 3.91vw;
  }
  
  .howWeWork-conteiner .button{
    width: 8.80vw;
    height: 2.60vw;
    margin-left: 0;
  }
  
  .howWeWorkRectangle-conteiner{
    width: 100%;
    height: 31.20vw;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 16.61vw;
    position: relative;
  }
  
  img.howWeWorkRectangle{
    width: 67.45vw;
  }
  
  .howWeWorkRectangle-conteiner-title, .howWeWorkRectangle-conteiner-content{
    position: absolute;
    left: 0;
    width: 100%;
    z-index: 2;
  }
  
  .howWeWorkRectangle-conteiner-title-content{
    display: flex;
    width: 61.98vw;
    margin-left: auto;
    margin-right: auto;
    padding-top: 3.07vw;
  }
  
  .howWeWorkRectangle-conteiner-title-content h2{
    font-family: 'Montserrat-Bold';
    font-weight: 700;
    color: #fff;
    font-size: 2.08vw;
    padding-right: 2.5vw;
  }
  
  .howWeWorkRectangle-conteiner-title-content p{
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 1.04vw;
    padding-left: 1.82vw;
    padding-top: 0.6vw;
  }
  
  .howWeWorkRectangle-conteiner-title-content .line{
    width: 0.05vw;
    height: 4vw;
    background-color: #31B536;
    margin-top: 0.65vw;
  }
  
  
  .howWeWorkRectangle-conteiner-content-items{
    display: flex;
    width: 61.98vw;
    margin-left: auto;
    margin-right: auto;
    padding-top: 10.5vw;
    justify-content: space-between;
  }
  
  .howWeWorkRectangle-item{
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    width: 30.36vw;
    height: 17.60vw;
  }
  
  .howWeWorkRectangle-item img {
    width: 2vw;
    padding-top: 1.25vw;
    padding-left: 1.93vw;
  }
  
  img.howWeWorkRectangle_itemTwo{
    width: 1.72vw;
  }
  
  .howWeWorkRectangle-item h2 {
    padding-left: 1.93vw;
    font-family: 'Montserrat-Bold';
    font-weight: 700;
    color: #fff;
    font-size: 1.30vw;
    padding-top: 1vw;
  }
  
  .howWeWorkRectangle-item p {
    padding-left: 1.93vw;
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 0.83vw;
    padding-top: 1vw;
    line-height: 1.1vw;
  }
  
  /*Strategies*/
  
  .strategies-title{
    padding-top: 3.91vw;
  }
  
  .strategies-title h2{
    font-family: 'Montserrat-Bold';
    font-weight: 700;
    color: #fff;
    font-size: 2.08vw;
    padding-bottom: 2.97vw;
  }
  
  .strategies-conteiner{
    display: flex;
    width: 100%;
    justify-content: space-between;
    
  }
  
  .strategies-conteiner-item{
    width: 21.41vw;
    height: 24.90vw;
    position: relative;
  }
  
  .strategies-conteiner-item img{
    width: 21.41vw;
    height: 24.90vw;
  }
  
  .strategies-conteiner-item-position{
    position: absolute;
    top: 13.49vw;
    left: 1.98vw;
  }
  
  .strategies-conteiner-item-content h2{
    font-family: 'Montserrat-Bold';
    font-weight: 700;
    color: #fff;
    font-size: 1.30vw;
  }
  
  .strategies-conteiner-item-content p{
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 0.83vw;
    padding: 1.46vw 0;
  }
  
  .strategies-button{
    width: 9.32vw;
    height: 2.60vw;
    margin-left: 0;
  }
  
  .item-width , .item-width img{
    width: 32.97vw;
  }
  
  .item-width {
    padding-top: 1.35vw;
  }
  
  
  /*feedback*/
  
  .ellipse_feedback{
    position: absolute;
    right: 0;
    width: 38vw;
    top: 123vw;
    z-index: -1;
  }
  
  .feedback-conteiner{
    width: 100%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 5.21vw;
  }
  
  .feedback-conteiner-title{
    margin-right: auto;
    padding-top: 4.7vw;
  }
  
  .feedback-conteiner-title h2{
    font-family: 'Montserrat-Bold';
    font-weight: 700;
    color: #fff;
    font-size: 2.29vw;
    padding-bottom: 1vw;
  }
  
  .feedback-conteiner-title p{
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 0.94vw;
  }
  
  .feedback-conteiner-content{
    width: 32.55vw;
    height: 20vw;
    background-color: rgba(39, 39, 39, 0.33);
    border-radius: 10px;
  }
  
  .feedback-conteiner-content h3{
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 0.83vw;
    text-align: center;
    padding-top: 1.3vw;
  }
  
  .feedback-conteiner-content input{
    font-family: 'Montserrat';
    font-weight: 300;
    color: #fff;
    font-size: 0.73vw;
    width: 27.71vw;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    border-radius: 10px;
    height: 2vw;
    background-color: transparent;
    border: 0.1vw solid rgba(39, 39, 39, 0.43);
    margin-top: 1vw;
  }
  
  .feedback-conteiner-content p{
    font-family: 'Montserrat';
    font-weight: 300;
    color:rgba(255, 255, 255, 0.5);
    font-size: 0.52vw;
    text-align: center;
    padding-top: 1.3vw;
    padding-bottom: 1.56vw;
  }
  
  .feedback-conteiner-content span{
    text-decoration: underline;
  }
  
  .feedback-button-conteiner{
    width:100%;
    display: flex;
  }
  
  .feedback-button{
    width: 8.80vw;
    height: 2.60vw;
    margin-left: auto;
    margin-right: auto;
  }
  
  ::placeholder {
    text-align: center;
  }
  
    /*footer*/
  
    .footer-container {
      position: relative;
      z-index: 2;
    }
  
    .footer-container-icon img{
      display: flex;
      margin-left: auto;
      margin-right: auto;
      width: 10.05vw;
      padding-top: 4.79vw;
    }
    
    .footer-container-contant{
      padding-top: 1vw;
      padding-bottom: 5.31vw;
    }
    
    .footer-container-contant h2, .footer-container-contant p{
      text-align: center;
      color: rgba(255, 255, 255, 0.9);
      font-size: 0.83vw;
      font-family: 'Montserrat';
      font-weight: 400;
    }
   
    .footer{
      position: relative;
      z-index: 2;
    }
  
